.form-group {
	margin: 0 -10px;
	@include clr;
	&:not(:last-child) {
		margin-bottom: 15px; }
	&__label,
	&__field {
		float: left;
		padding-left: 10px;
		padding-right: 10px; }
	&__label {
		width: 30%;
		height: 40px;
		color: lighten($darkGray, 30%);
		@include vertical;
		text-align: right;
		@include sm {
			width: 100%;
			height: auto;
			text-align: left;
			margin-bottom: 5px; }
		span {
			display: inline-block;
			vertical-align: middle;
			@include sm {
				vertical-align: top; } } }
	&__field {
		width: 70%;
		@include sm {
			width: 100%; } }
	&__label span,
	&__value {
		@include f(16px, 400, 20px); }
	&__value {
		padding-top: 10px;
		padding-bottom: 10px;
		@include sm {
			padding-bottom: 0; } }
	&__row {
		margin: 0 -5px;
		@include clr;
		@include sm {
			padding-top: 5px; } }
	&__col {
		float: left;
		padding: 5px;
		width: 50%;
		@include xs {
			width: 100%; } } }
