body {
  position: relative;
  padding-top: 60px;
  padding-bottom: 60px;
  min-width: 320px;
  color: #333;
  background: #f8f8f8;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body {
  height: 100%;
}

.container {
  overflow: auto;
  height: 100%;
}

[hidden] {
  display: none !important;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn:hover .icon-right {
  transform: translate3d(50%, 0, 0);
}

.btn:hover .icon-left {
  transform: translate3d(-50%, 0, 0);
}

.btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn .icon-right,
.btn .icon-left {
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
  font-size: 80%;
  margin-top: 22px;
  transition: transform .175s;
}

.btn-icon {
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #333;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.25s, box-shadow 0.25s;
}

.btn-icon:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .btn-icon:hover {
  background: rgba(0, 0, 0, 0.05);
}

.btn-icon:active {
  background: rgba(0, 0, 0, 0.1) !important;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2) !important;
}

.btn-icon_red {
  color: #ec4a41;
}

.btn-icon__icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 30px;
}

.input {
  padding: 0 12px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:not([readonly]):focus {
  border-color: #2e99db;
}

.input:disabled, .input[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.user-data__title {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  margin-bottom: 4px;
}

.user-data__subtitle {
  font-size: 12px;
  font-weight: 300;
  line-height: 16px;
}

.nav {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  background: rgba(67, 76, 85, 0.8);
  transform: translate3d(0, 0, 0);
  transition: opacity 0.25s, visibility 0.25s;
  transition-delay: 0.2s;
}

.nav.is-open {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.nav.is-open .nav__inner {
  transform: translate3d(0, 0, 0);
  transition-delay: 0.2s;
}

.nav.is-open .nav__header,
.nav.is-open .nav__overlay {
  pointer-events: auto;
}

.nav__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.nav__inner {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: auto;
  width: 300px;
  color: #fff;
  background: #434c55;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.25s ease 0.25s;
  transition-delay: 0s;
}

.nav__header {
  position: relative;
  display: block;
  padding: 0 20px 0 50px;
  width: 100%;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
  text-align: left;
  cursor: pointer;
  pointer-events: none;
  transition: background 0.25s;
}

.nav__header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .nav__header:hover {
  background: #1f7db7;
}

.nav__header:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.nav__header strong,
.nav__header .icon {
  display: inline-block;
  vertical-align: middle;
}

.nav__header strong {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.nav__header .icon {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -0.5em;
  fill: currentColor;
  font-size: 20px;
}

.nav__header span {
  position: absolute;
  left: 50px;
  right: 0;
  bottom: 5px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
}

.nav__body {
  padding: 20px;
}

.nav__user-data {
  padding-bottom: 20px;
}

.nav__list {
  margin: 0 -20px;
  border-top: 1px solid #4C5560;
  color: #7d838b;
}

.nav__link {
  position: relative;
  display: block;
  padding: 5px 20px 5px 50px;
  border-bottom: 1px solid #4C5560;
  color: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 40px;
  transition: background 0.25s;
}

.no-touch .nav__link:hover {
  background: #4c5560;
}

.nav__link.is-active {
  color: #fff;
  background: #4c5560;
}

.nav__link .icon {
  display: block;
  margin: 0 auto;
  fill: currentColor;
}

.nav__icon {
  position: absolute;
  top: 50%;
  left: 5px;
  width: 40px;
  margin-top: -0.5em;
  font-size: 24px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.header__btn-help {
  right: 0;
  text-align: center;
  font-size: 0;
}

.header__btn-help:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__btn-help .icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 30px;
}

.header__btn-nav {
  left: 0;
}

.header__btn-nav:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -1px 0 0 -14px;
  border-top: 2px solid #fff;
  width: 28px;
}

.header__btn-nav span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -11px 0 0 -14px;
  width: 28px;
  height: 22px;
}

.header__btn-nav span:before, .header__btn-nav span:after {
  content: '';
  position: absolute;
  left: 0;
  border-top: 2px solid #fff;
  width: 100%;
}

.header__btn-nav span:before {
  top: 0;
}

.header__btn-nav span:after {
  bottom: 0;
}

.header__btn-help, .header__btn-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 60px;
  color: #fff;
  background: none;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.no-touch .header__btn-help:hover, .no-touch .header__btn-nav:hover {
  background: #1f7db7;
}

.header__btn-help:active, .header__btn-nav:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header__inner {
  padding: 0 60px;
  height: 60px;
  text-align: center;
  font-size: 0;
}

.header__inner:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.header__logo {
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
  width: 40px;
  height: 40px;
}

.footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 20px;
  height: 60px;
  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  font-size: 0;
}

.footer:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.footer__list {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  color: #333;
  font-size: 0;
  text-align: center;
}

.footer__item, .footer__link {
  display: inline-block;
  vertical-align: top;
}

.footer__item {
  position: relative;
  padding: 0 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.footer__link {
  color: #2e99db;
}

.no-touch .footer__link:hover {
  text-decoration: underline;
}

.select {
  position: relative;
  text-align: left;
  user-select: none;
}

.select__header {
  position: relative;
  padding: 0 36px 0 12px;
  border: 1px solid #333;
  width: 100%;
  height: 40px;
  background: #fff;
  appearance: none;
  transition: border-color 0.25s;
  cursor: pointer;
  transition: border-color 0.25s;
}

.select.is-open .select__header {
  border-color: #2e99db;
}

.select__value {
  position: relative;
  z-index: 2;
  height: 38px;
  font-size: 16px;
  font-weight: 400;
  line-height: 38px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.select__arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  right: 12px;
  fill: currentColor;
  font-size: 8px;
  margin-top: -0.4em;
  transition: transform 0.25s;
}

.select.is-open .select__arrow {
  transform: rotateX(180deg);
}

.select__body {
  position: absolute;
  z-index: 20;
  top: 100%;
  left: 0;
  right: 0;
  display: none;
  overflow: auto;
  margin-top: 4px;
  border: 1px solid #333;
  max-height: 200px;
  background: #fff;
}

.select.is-open .select__body {
  display: block;
}

.select__option {
  padding: 5px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s, background 0.25s;
}

.no-touch .select__option:hover {
  color: #2e99db;
  background: rgba(0, 0, 0, 0.05);
}

.select__option:active {
  background: rgba(0, 0, 0, 0.1) !important;
}

.select__option.is-active {
  color: #fff !important;
  background: #2e99db !important;
}

.form-group {
  margin: 0 -10px;
}

.form-group:after {
  content: " ";
  display: table;
  clear: both;
}

.form-group:not(:last-child) {
  margin-bottom: 15px;
}

.form-group__label, .form-group__field {
  float: left;
  padding-left: 10px;
  padding-right: 10px;
}

.form-group__label {
  width: 30%;
  height: 40px;
  color: gray;
  text-align: center;
  font-size: 0;
  text-align: right;
}

.form-group__label:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 580px) {
  .form-group__label {
    width: 100%;
    height: auto;
    text-align: left;
    margin-bottom: 5px;
  }
}

.form-group__label span {
  display: inline-block;
  vertical-align: middle;
}

@media only screen and (max-width: 580px) {
  .form-group__label span {
    vertical-align: top;
  }
}

.form-group__field {
  width: 70%;
}

@media only screen and (max-width: 580px) {
  .form-group__field {
    width: 100%;
  }
}

.form-group__label span, .form-group__value {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.form-group__value {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media only screen and (max-width: 580px) {
  .form-group__value {
    padding-bottom: 0;
  }
}

.form-group__row {
  margin: 0 -5px;
}

.form-group__row:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 580px) {
  .form-group__row {
    padding-top: 5px;
  }
}

.form-group__col {
  float: left;
  padding: 5px;
  width: 50%;
}

@media only screen and (max-width: 480px) {
  .form-group__col {
    width: 100%;
  }
}

.form {
