  line-height: 20px;
}

@media only screen and (max-width: 480px) {
  .sm-info__label, .sm-info__value {
    width: 50%;
  }
}

.settings {
  position: relative;
  height: 100%;
}

.settings__footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 15px;
  text-align: center;
}

.settings__btn {
  max-width: 200px;
}

.settings__inner {
  overflow: auto;
  padding: 20px 40px;
  height: 100%;
}

@media only screen and (max-width: 580px) {
  .settings__inner {
    padding: 20px;
  }
}

.radio {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 30px;
  min-height: 30px;
}

.no-touch .radio:hover .radio__title {
  color: #2e99db;
}

.radio input {
  display: none;
}

.radio input:checked ~ .radio__icon {
  border-color: #2e99db;
}

.radio input:checked ~ .radio__icon:before {
  opacity: 1;
  background: #2e99db;
}

.radio input:disabled ~ .radio__icon, .radio input:disabled ~ .radio__title {
  opacity: 0.5;
  cursor: not-allowed;
}

.radio__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #333;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: border-color 0.25s;
}

.radio__icon:before {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  opacity: 0;
  background: #333;
  transition: opacity 0.25s, background 0.25s;
}

.radio__title {
  display: inline-block;
  padding: 5px 0 5px 40px;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  cursor: pointer;
  transition: color 0.25s;
}

.section {
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.1);
}

.section:not(:first-child) {
  margin-top: 20px;
}

.section__header {
  position: relative;
  padding: 20px 50px 20px 20px;
}

.section__header.is-active .section__arrow {
  transform: rotateX(180deg);
}

.section__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.section__title:after {
  content: '';
  display: block;
  margin-top: 7px;
  border-bottom: 2px solid rgba(51, 51, 51, 0.3);
  max-width: 80px;
}

.section__body {
  padding: 0 20px 20px;
}

.section__footer {
  margin-top: 20px;
  text-align: center;
}

.section__btn {
  max-width: 200px;
}

.section__arrow {
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -0.5em;
  fill: #333;
  font-size: 12px;
  transition: transform 0.25s;
}

.modal {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal_sm .modal__container {
  max-width: 400px;
}

.modal__container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 600px;
}

.modal__close {
  position: absolute;
  z-index: 2;
  top: 5px;
  right: 5px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #fff;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.25s, box-shadow 0.25s;
}

.modal__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .modal__close:hover {
  background: rgba(0, 0, 0, 0.05);
}

.modal__close:active {
  background: rgba(0, 0, 0, 0.1) !important;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2) !important;
}

.modal__close .icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 18px;
}

.modal-content {
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.1);
}

.modal-content__header {
  padding: 5px 60px 5px 20px;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
  text-align: left;
}

.modal-content__header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal-content__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.modal-content__body, .modal-content__footer {
  padding: 20px;
}

.modal-content__body {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  text-align: left;
}

.modal-content__footer {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  text-align: right;
}

[data-accordion-item] [data-accordion-control] {
  cursor: pointer;
  user-select: none;
}

[data-accordion-item] [data-accordion-content] {
  display: none;
}

.loader {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.loader.is-open {
  opacity: 1;
  visibility: visible;
}

.loader__icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  font-size: 150px;
}

.loader__icon:before, .loader__icon:after {
  content: '';
  position: absolute;
  top: 13%;
  left: 13%;
  right: 13%;
  bottom: 13%;
  border-radius: 50%;
  width: 74%;
  height: 74%;
  background: rgba(0, 0, 0, 0.3);
  transform: scale(0);
  animation: scale 2s cubic-bezier(0.1, 0.2, 0.3, 1) infinite;
}

.loader__icon:after {
  animation-delay: 1s;
}

@keyframes scale {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  90% {
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.subtitle {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.text-default {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.3;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}
